.navbar_cont{
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 2rem;
    padding-top: 1rem;
}
.navbar-menu{
    display: flex;
    list-style-type: none;
}
a{
    text-decoration: none;
    color: #181E25;
}
.navbar-item{
    padding-right: 2rem;
    font-size: 22px;
    font-weight: 400;
}
.navbar_youtube{
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    padding: 12px 24px;
    grid-gap: 5px;
    gap: 5px;
    background-color: white;
    width: 140px;
    height: 48px;
    border: 1px solid #DEE4ED;
    border-radius: 3px;
    margin-right: 70px;
    margin-left: 10px;
    font-size: 1.2rem;
    color: #181E25;
    cursor: pointer;
    margin-top: 20px;
}
.navbar_hamburger{
    display: none;
}
.chevronright{
    padding-left: 10px;
    padding-top: 3px;
}

@media screen and (max-width: 1100px){
    .navbar-menu{
        position: fixed;
        left: -100%;
        top: 70px;
        flex-direction: column;
        justify-content: center;
        background: #11151c;
        width: 100%;
        height: 100%;
        text-align: center;
        transition: .5s;
        z-index:3;
        color: white;
    }
    a{
        text-decoration: none;
        color: white;
    }
    .navbar_hamburger{
        display: flex;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    .navbar-menu.active{
        left: 0;
    }
    .navbar-item{
        margin: 0.5rem;
        justify-content: center;
        color: #181E25;
       font-size: 20px;
    }
    .navbar_youtube{
        margin: 0.5rem;
        justify-content: center;
        margin-left: 40%;
       font-size: 20px;
    }
}
@media screen and (max-width: 400px){
    .navbar_hamburger{
        display: flex;
        margin-left: 2rem;
    }
}
