/* 
    Mentorship
*/

.mentorship-section {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mentorship-card {
    display: flex;
    grid-gap: 40px;
    gap: 40px;
    max-width: 1200px;
    margin: auto;
    padding: 1rem;

}

.mentorship-card .details {
    order: 2;
    width: 60%;
}

.mentorship-section h2 {
    text-align: left;
    color: #181E25;
    font-size: 36px;
}

.mentorship-section p {
    text-align: left;
    font-size: 20px;
    color: #5F7086;
    margin-bottom: 24px;
}

.mentorship-section a {
    display: inline-block;
    height: 53px;
    width: 197px;
    border-radius: 4px;
    padding: 12px 24px;
    background: #4094EC;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
    text-align: center;
}

.mentorship-section .thumbnail {
    display: block;
    order: 1;
    max-width: 580px;
    object-fit: contain;
}

.roadmaps-header .arrow-down {
    height: 18px;
    width: 14px;
    margin-left: 12px;
    top: 6px;
    border-radius: 0px;
}

.mentorship-section .arrow-right {
    height: 12px;
    width: 16px;
    margin-left: 12px;
    top: 6px;
    border-radius: 0px;
}
